<template>
	<view class="content">
		<image class="logo" src="/static/logo.png"></image>
		<view class="text-area">
			<text class="title">{{title}} - {{num}}</text>
		</view>
		<w-picker :visible.sync="visible" mode="region" :current="true"></w-picker>
	</view>
</template>

<script>
	import {
		mapState
	} from 'vuex'
	import wPicker from '@/components/w-picker/w-picker.vue';
	export default {
		components: {
			wPicker
		},
		data() {
			return {
				title: 'Hello'
			}
		},
		computed: {
			...mapState({
				num: state => state.num
			})
		},
		onLoad() {
			// 测试功能
			this.$nextTick(() => {
				this.testGlobal()
				this.testStorage()
				this.testRequest()
				setTimeout(() => {
					this.testStore()
				}, 2000)
			})
		},
		methods: {
			testRequest () {
				this.$request.post('https://www.hongshaoli.com/list').then((res)=>{
					console.log(res)
				})
			},
			testStore () {
				this.$store.dispatch('setNum', 2)
			},
			testGlobal () {
				console.log(getApp().globalData.test)
			},
			testStorage () {
				this.$storage.setStorageSync('test', '1234')
			}
		}
	}
</script>

<style lang="less" scoped>
	.content {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;

		.logo {
			height: 200rpx;
			width: 200rpx;
			margin-top: 200rpx;
			margin-left: auto;
			margin-right: auto;
			margin-bottom: 50rpx;
		}

		.text-area {
			display: flex;
			justify-content: center;
		}

		.title {
			font-size: 36rpx;
			color: #8f8f94;
		}
	}
</style>
